@mixin bodyELementsSize {
  width: 3%;
  height: 15%;
}

@mixin rightSide {
  transform: translate(-50%, -50%) rotate(-30deg);
}

@mixin leftSide {
  transform: translate(-50%, -50%) rotate(30deg);
}

@mixin btn-size {
  font-size: 22px;
  text-transform: uppercase;
  @media screen and (min-height: 360px) {
    font-size: 28px;
  }
  @media screen and (min-width: 768px) {
    font-size: 42px;
  }
}

@mixin chars {
  font-size: 30px;
  text-transform: uppercase;
  @media screen and (min-height: 360px) {
    font-size: 38px;
  }
  @media screen and (min-width: 768px) {
    font-size: 44px;
  }
}

@mixin flex-justify-center {
  display: flex;
  justify-content: center;
}

@mixin btn-style {
  background-color: var(--color-primary);
  border-radius: 10px;
  border: none;

  font-weight: 700;
  padding: 8px;
}
